@font-face {
  src: local('Kooka-Regular'), url(/2-be-wed/static/media/Kooka-Regular.2599e0b5.ttf), format('ttf');
  font-family: 'Kooka-Regular';
}

* {
  background-color: black;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Kooka-Regular', Arial, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  src: local('Kooka-Regular'), url(/2-be-wed/static/media/Kooka-Regular.2599e0b5.ttf), format('ttf');
  font-family: 'Kooka-Regular';
}

:root {
  /* root colors, these are all of the flowers in the canva logo i made */
  --dark-green: #5b846d;
  --orange: #ef7525;
  --lilac: #cb6ce6;
  --red: #ef1963;
  --purple: #8c52ff;
  --blue: #80bef8;
  --lime: #c9e265;
  --yellow: #ffde59;
}

.App {
  text-align: center;
  background-color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@font-face {
  src: url(/2-be-wed/static/media/Kooka-Regular.2599e0b5.ttf);
  font-family: 'Kooka-Regular';
}

header > h1 {
  font-family: 'Kooka-Regular';
  color: white;
  font-size: 8rem;
}

header > p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 2rem;
  color: white;
  font-weight: bold;
}

i {
  font-size: 4rem;
}

.dark-green {
  color: var(--dark-green);
}
.orange {
  color: var(--orange);
}
.lilac {
  color: var(--lilac);
}
.red {
  color: var(--red);
}
.purple {
  color: var(--purple);
}
.blue {
  color: var(--blue);
}
.lime {
  color: var(--lime);
}
.yellow {
  color: var(--yellow);
}

